import App from './../App'

const routes = [
    {
        path: '/',
        component: App,
        children: [
            { path: '/', redirect: '/home' },
            { path: '/home',
              component: (resolve) => {
                require(['./../components/Home.vue'], resolve);
            } },
            { path: '/services',
              component: (resolve) => {
                require(['./../components/services'], resolve);
            } },
            { path: '/contact',
            component: (resolve) => {
              require(['./../components/contact'], resolve);
            } },
            { path: '/chef',
            component: (resolve) => {
              require(['./../components/chef'], resolve);
            } },
            { path: '/rest',
            component: (resolve) => {
              require(['./../components/rest'], resolve);
            } },
            { path: '/chefslist',
            component: (resolve) => {
              require(['./../components/chefslist'], resolve);
            } },
        ]
    }
]

export default routes



